* {
  box-sizing: border-box;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  color: #212121;
  background-size: cover;
  margin: 0;
  padding-bottom: 20px;
  line-height: 1.5;
  position: relative;
}

.wrapper {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

.search-form {
  width: 100%;
  height: 40px;
  z-index: 100;
  background-image: linear-gradient(to top, rgba(234, 95, 3, .5), rgba(112, 210, 8, .3));
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
  position: fixed;
}

.search-form__input {
  text-align: center;
  color: #f41717;
  width: 40%;
  height: 60%;
  border: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-right: 10px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
}

.search-form__btn {
  height: 60%;
  color: #eff3ee;
  background-image: linear-gradient(to top, rgba(7, 23, 197, .5), rgba(47, 48, 58, .3));
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
}

.gallery {
  max-width: 1600px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: auto;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  display: grid;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.gallery__link {
  transition: transform var(--animation-duration) var(--timing-function);
  text-decoration: none;
  display: block;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .35);
}

.gallery__link:hover {
  cursor: zoom-in;
  transform: scale(1.03);
}

.info {
  width: 100%;
  background-color: rgba(47, 48, 58, .7);
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 5px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.info-item {
  letter-spacing: .04em;
  text-align: center;
  color: #fafafa;
  margin: 0;
  font-size: 11px;
  line-height: 1.15;
  display: block;
}

.load-more {
  color: #eff3ee;
  width: 30%;
  height: 25px;
  background-image: linear-gradient(to top, rgba(47, 48, 58, .5), rgba(47, 48, 58, .2));
  border: rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.autoload_text {
  color: #1b1fea;
  margin-left: 15px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
}

